import { Dispatch } from "react";
import { AppActions } from "../_types";
import {
    REQUEST_PLAYER,
    PlayerDataInfo,
    RESET_PLAYER,
    REQUEST_PLAYMODE,
    START_PLAYER,
    STOP_PLAYER,
} from "../_types/player.types";
import { lessonCompiler } from "../_helpers/playerApi";
import { API, APICancelToken } from "../_helpers/api";
import Song from "../components/Layout/Melodition/Song";

let cancel: any;

export const requestPlayer = (info: PlayerDataInfo) => (
    (dispatch: Dispatch<AppActions>) => {
        lessonCompiler(info).then((currentLesson) => {
            if (currentLesson) {
                console.log(currentLesson)
                if (currentLesson.songs.length > 0) {
                    const currentSong = new Song(currentLesson.audioContext);
                    currentLesson.songs.forEach((song, index) =>
                        API.get(`/song/v2/${song.id}`, {
                            cancelToken: new APICancelToken((c) => {
                                cancel = c;
                            })
                        }).then((url) =>
                            API({
                                url: url.data,
                                method: "get",
                                headers: {},
                                responseType: "arraybuffer",
                                cancelToken: new APICancelToken((c) => {
                                    cancel = c;
                                })
                            }).then((res) => {
                                currentSong?.loadBuffer({ buffer: res.data, vol: song.vol })
                                    .then(() => {
                                        if (currentLesson.songs.length === index + 1) {
                                            currentLesson.currentSong = currentSong;
                                            currentLesson.loaded = true;
                                            dispatch({
                                                type: REQUEST_PLAYER,
                                                payload: currentLesson
                                            });
                                        }
                                    })
                            }).catch((bufferError) => console.log(bufferError))
                        ).catch((urlError) => console.log(urlError))
                    );
                } else {
                    currentLesson.loaded = true;
                    dispatch({ type: REQUEST_PLAYER, payload: currentLesson });
                }
            }
        }).catch(err => console.log(err))
    }
)
export const startPlayer = () => (
    (dispatch: Dispatch<AppActions>) => {
        dispatch({ type: START_PLAYER })
    }
)
export const stopPlayer = () => (
    (dispatch: Dispatch<AppActions>) => {
        dispatch({ type: STOP_PLAYER })
    }
)
export const resetPlayer = () => (
    (dispatch: Dispatch<AppActions>) => {
        if (typeof cancel === 'function') cancel();
        dispatch({ type: RESET_PLAYER })
    }
)
export const changeMode = (mode: boolean) => (
    (dispatch: Dispatch<AppActions>) => {
        dispatch({ type: REQUEST_PLAYMODE, mode });
    }
)