import React, { useLayoutEffect, useRef, useState } from "react";
import Game from "../../Layout/Game/Game";
import { CircularProgress } from "@material-ui/core";
import Melo from "../../Layout/Melodition/Melodition";

let gameLoaded = false;

const PianoLines: React.FC = () => {
  const [loading, setLoading] = useState(true);

  const firstCanvas = useRef(null);
  const lastCanvas = useRef(null);

  const drawNow = () => {
    const first = firstCanvas?.current;
    const last = lastCanvas?.current;
    if (first && last && !gameLoaded) {
      console.log("Render Game");
      Melo.game = new Game(first, last);
      gameLoaded = true;
    }
  };

  useLayoutEffect(() => {
    let reset: any = null;
    const updateDimensions = () => {
      setLoading(true);
      gameLoaded = false;
      Melo.seekValue = 0;
      Melo.stopPlay();
      clearTimeout(reset);
      reset = setTimeout(() => {
        setLoading(false);
      }, 500);
    };
    window.addEventListener("resize", updateDimensions);
    updateDimensions();
    return () => window.removeEventListener("resize", updateDimensions);
  }, []);

  const detectSize = (height: number) => {
    const size = height;
    if (size > 700) {
      Melo.tapSize = size / 16;
    } else if (size > 500) {
      Melo.tapSize = size / 16;
    } else if (size > 300) {
      Melo.tapSize = size / 16;
    } else if (size > 100) {
      Melo.tapSize = size / 16;
    } else {
      Melo.tapSize = size / 16;
    }
  };

  const div = Melo.keyboardDiv?.getBoundingClientRect();
  const height = div ? div.height : 600;
  const calcWidth = div ? div.width : 800;
  const calcHeight = Math.floor(window.innerHeight - (height + 64));
  const getHeight = (num: number | undefined) => (num ? num * 4 + 8 : 136);
  detectSize(calcHeight);
  return loading ? (
    <div
      style={{
        width: calcWidth,
        height: calcHeight,
        display: "flex",
        boxShadow: "inset 0 1px 4px 0px black",
      }}
    >
      <CircularProgress color="secondary" style={{ margin: "auto" }} />
    </div>
  ) : (
    <div
      style={{
        width: calcWidth,
        height: calcHeight,
        position: "relative",
        boxShadow: "inset 0 1px 4px 0px black",
      }}
    >
      <canvas
        ref={firstCanvas}
        width={calcWidth}
        height={calcHeight}
        style={{ position: "absolute", top: 0, left: 0, zIndex: 2 }}
        onWheel={(e) => Melo.rakeMouseWheel(e)}
      />
      <canvas
        ref={lastCanvas}
        width={calcWidth}
        height={calcHeight}
        style={{ zIndex: 1 }}
      />
      <svg
        ref={() => drawNow()}
        width={calcWidth + 7}
        height={getHeight(Melo.tapSize)}
        onWheel={(e) => Melo.rakeMouseWheel(e)}
        style={{ bottom: 0, left: -7, zIndex: 3, position: "absolute" }}
      >
        <style>{"@keyframes draw{to{stroke-dashoffset:0}}"}</style>
        <polygon points={"0,0 10,8 0,16"} fill={"red"} />
        <line
          style={{
            strokeDasharray: calcWidth + 7,
            strokeDashoffset: calcWidth + 7,
            animation: "draw 1s linear forwards",
          }}
          x1={0}
          y1={8}
          x2={calcWidth + 7}
          y2={8}
          strokeWidth={2.5}
          stroke={"red"}
        />
      </svg>
    </div>
  );
};

export default PianoLines;
