import Melo from "../Melodition/Melodition";
import { BackMover, MusicLine, Note } from "./Canvas";

export default class Game {
  public counter = 0;
  private gap: number;
  private margin: number;
  private width: number;
  private height: number;
  private notes: Note[] = [];
  private image: HTMLImageElement;
  private myObstacles: Note[][] = [];
  private backMover: BackMover[] = [];
  private fullSongCanvas: Note[] = [];
  private context: CanvasRenderingContext2D;
  private backCtx: CanvasRenderingContext2D;
  private notePos: { left: number, center: number, width: number }[] = [];
  constructor(canvas: HTMLCanvasElement, backCanvas: HTMLCanvasElement) {
    this.image = new Image();
    this.width = canvas.width;
    this.height = canvas.height;
    this.gap = Melo.tapSize * 12;
    this.margin = Melo.tapSize * 4;
    this.context = canvas.getContext("2d")!;
    this.backCtx = backCanvas.getContext("2d")!;
    this.backCtx.clearRect(0, 0, this.width, this.height);
    this.clear();
    this.drawImage();
    this.drawBackgroundLine();
    this.drawNote();
  }
  assigner = (options: Partial<any>) => {
    Object.assign(this, options);
  }
  playGame = (y: number) => {
    this.clear();
    this.updateBacks(y)
    this.updateNotes(y)
  }
  clear = () => {
    this.context.clearRect(0, 0, this.width, this.height);
  }
  pitchName = (pitch: number) => {
    let n = pitch % 12;
    let t = "C";
    if (n === 1) {
      t = "Db";
    }
    if (n === 2) {
      t = "D";
    }
    if (n === 3) {
      t = "Eb";
    }
    if (n === 4) {
      t = "E";
    }
    if (n === 5) {
      t = "F";
    }
    if (n === 6) {
      t = "Gb";
    }
    if (n === 7) {
      t = "G";
    }
    if (n === 8) {
      t = "Ab";
    }
    if (n === 9) {
      t = "A";
    }
    if (n === 10) {
      t = "Bb";
    }
    if (n === 11) {
      t = "B";
    }
    return "" + t;
  }
  drawImage = () => {
    const myLine = document.createElement("canvas").getContext("2d")!;
    myLine.canvas.width = this.width;
    myLine.canvas.height = this.height;
    myLine.save();
    Array.from(Array(17), (_, i) => i).forEach((_, v) => {
      const lineWidth =
        v % 16 === 0
          ? 1.5
          : v % 8 === 0
            ? 1.2
            : v % 4 === 0
              ? 1
              : v % 2 === 0
                ? 0.8
                : 0.5;
      const strokeStyle =
        v % 16 === 0
          ? "black"
          : v % 8 === 0
            ? "black"
            : v % 4 === 0
              ? "rgba(0,0,0,0.5)"
              : v % 2 === 0
                ? "rgba(0,0,0,0.5)"
                : "rgba(0,0,0,0.2)";
      const yPos = v * Melo.tapSize;
      const line = new MusicLine({
        width: this.width,
        color: strokeStyle,
        x: 0,
        y: yPos,
        lw: lineWidth,
      });
      line.draw(myLine);
    });
    this.image.src = myLine.canvas.toDataURL("image/png");
    let firstLine: BackMover;
    this.image.onload = () => {
      if (!firstLine) {
        firstLine = new BackMover(0, this.height, this.margin);
        firstLine.update(this.context, this.image);
      }
    };
  }
  drawBackgroundLine = () => {
    const lineWidth =
      Melo.keyRefs[50].getBoundingClientRect().x -
      (Melo.keyRefs[48].getBoundingClientRect().x +
        Melo.keyRefs[48].getBoundingClientRect().width);
    const h = this.height;
    const keyXPos = Melo.keyboardDiv?.getBoundingClientRect().left
    const padLeft = keyXPos ? keyXPos : 100;
    Melo.keyRefs.forEach(key => {
      this.backCtx.beginPath();
      const rect = key.getBoundingClientRect();
      const x = rect.x - padLeft;
      const w = rect.width;
      const centerX = x + w / 2;
      if (key.classList.contains("C") || key.classList.contains("F")) {
        this.notePos.push({ left: x, center: centerX - lineWidth * 3.5, width: w });
        if (!key.classList.contains("first")) {
          this.backCtx.moveTo(x, 0);
          this.backCtx.lineTo(x, h);
          this.backCtx.lineWidth = lineWidth;
          this.backCtx.strokeStyle = "rgba(50,50,50,0.8)";
          this.backCtx.stroke();
        }
      } else if (key.classList.contains("last")) {
        this.notePos.push({ left: x, center: centerX, width: w });
        this.backCtx.moveTo(x, 0);
        this.backCtx.lineTo(x, h);
        this.backCtx.lineWidth = lineWidth;
        this.backCtx.strokeStyle = "rgba(50,50,50,0.8)";
        this.backCtx.stroke();
      } else if (key.id === "black") {
        this.notePos.push({ left: x, center: centerX, width: w });
        this.backCtx.rect(x, 0, w, h);
      } else if (key.classList.contains("E") || key.classList.contains("B"))
        this.notePos.push({ left: x, center: centerX + lineWidth * 3.5, width: w });
      else if (key.classList.contains("A"))
        this.notePos.push({ left: x, center: centerX + lineWidth * 1.8, width: w });
      else if (key.classList.contains("G"))
        this.notePos.push({ left: x, center: centerX + lineWidth * 1.4, width: w });
      else this.notePos.push({ left: x, center: centerX, width: w });
      this.backCtx.fillStyle = "rgba(0,0,0,0.2)";
      this.backCtx.fill();
    })
  }
  drawNote = () => {
    const tap = Melo.tapSize;
    const showTrack: {
      beat: number,
      pitch: number,
      shift: number,
      length: number,
    }[] = [];
    Melo.storeTracks.forEach(p => {
      if (p.track === Melo.TrackID) {
        showTrack.push({
          beat: p.beat - 12,
          pitch: p.pitch,
          shift: p.shift,
          length: p.length,
        });
      }
    });
    Array.from(Array(Melo.sL), (_, i) => i).forEach((_, m) => {
      const r = this.notePos[1].width;
      const y = tap * (16 * m);
      this.myObstacles[m] = [];
      showTrack.forEach((p) => {
        if (p.beat >= m * 16) {
          if (p.beat < (m + 1) * 16) {
            const yy = y + tap * (p.beat % 16);
            const cc = this.notePos[p.pitch].center;
            const le = yy + tap * p.length;
            const note = new Note({
              width: r,
              height: le,
              color: "yellow",
              x: cc,
              y: yy,
              end: le + this.height * 3,
              text: this.pitchName(p.pitch),
              note: p.pitch + 36,
              gap: this.gap,
              tapSize: Melo.tapSize
            });
            this.myObstacles[m].push(note);
            this.fullSongCanvas.push(note);
          }
        }
      });
    });
  }
  drawBack = () => {
    this.backMover = [];
    for (let i = 0; i < 3; i++) {
      this.backMover.push(new BackMover(i, this.height, this.margin));
    }
  }
  pushNote = (num?: number) => {
    if (num) this.counter = num;
    if (
      this.myObstacles[this.counter] &&
      this.myObstacles[this.counter].length > 0
    ) {
      this.myObstacles[this.counter].forEach((g) => {
        this.notes.push(g);
      });
    }
    this.counter++;
    this.backMover.push(new BackMover(this.counter + 2, this.height, this.margin));
  }
  updateNotes = (y: number) => {
    this.notes.forEach((note, i) => {
      note.y = y;
      note.update(this.context);
      if (note.end < y) {
        this.notes.splice(i, 1);
        // missing notes
        // if (!note.hit)
        // 	this.score.push([note.note, 'miss'])
      }
      if (this.gap - note.y <= note.start) {
        let hit: string;
        Melo.touchedKeys.forEach((touch) => {
          if (note.note === touch.note) {
            if (
              touch.y > note.beg - Melo.tapSize &&
              touch.y < note.beg + Melo.tapSize
            ) {
              note.hit = y - this.gap + Melo.tapSize * 0.1;
              if (
                touch.y - Melo.tapSize * 0.3 <= note.beg &&
                touch.y + Melo.tapSize * 0.3 >= note.beg
              ) {
                hit = "perfect";
                note.color = "green";
              } else if (touch.y > note.beg) {
                hit = "late";
                note.color = "purple";
              } else if (touch.y < note.beg) {
                hit = "early";
                note.color = "orange";
              }
              if (!touch.hit) {
                touch["hit"] = hit;
                Melo.score.push([note.note, touch.hit]);
                console.log(Melo.score);
              }
            }
          }
        });
        note.move();
      }
    });
  }
  updateBacks = (y: number) => {
    this.backMover.forEach((back, i) => {
      back.y = y;
      back.update(this.context, this.image);
      if (back.y > back.end) {
        this.backMover.splice(i, 1);
      }
    });
  }
  updateFullSong = (y: number) => {
    this.clear();
    Array.from(Array(Melo.sL), (_, i) => i).forEach((_, m) => {
      this.context.drawImage(this.image, 0, Math.floor(y - m * this.image.height - Melo.tapSize * 4));
    });
    this.fullSongCanvas.forEach(note => {
      note.y = y;
      note.update(this.context);
    });
  }
};