import Melo from "../components/Layout/Melodition/Melodition";
import {
  PlayerActionTypes,
  REQUEST_PLAYER,
  RESET_PLAYER,
  REQUEST_PLAYMODE,
  START_PLAYER,
  STOP_PLAYER
} from "../_types/player.types";

const defaultPlayer = {
  playMode: false,
  loading: true,
  onAir: false,
  octave: 5
};

export const playerReducer = (
  state = defaultPlayer,
  action: PlayerActionTypes
): typeof defaultPlayer => {
  switch (action.type) {
    case REQUEST_PLAYER:
      Melo.assigner(action.payload)
      return { ...state, loading: false };
    case REQUEST_PLAYMODE:
      Melo.assigner({ playMode: action.mode })
      return { ...state, playMode: action.mode, loading: false };
    case START_PLAYER:
      Melo.startPlay()
      return { ...state, onAir: Melo.onAir };
    case STOP_PLAYER:
      Melo.stopPlay()
      return { ...state, onAir: Melo.onAir };
    case RESET_PLAYER:
      return { ...state, loading: true };
    default:
      return state;
  }
};
