import React, { useState } from "react";
import {
  Drawer,
  Grid,
  IconButton,
  Slider,
  makeStyles,
  Typography,
  createStyles,
  Theme,
} from "@material-ui/core";
import {
  ArrowBackIos,
  FormatListNumberedRtl,
  PlayCircleOutline,
} from "@material-ui/icons";
import { PathFinder } from "../../../_helpers/constants";
import { ICourse } from "../../../_types/course.types";
import { ILesson } from "../../../_types/lesson.types";
import SongListSelector from "./SongListSelector";
import Fingers from "../../Instruments/Finger/Fingers";
import Piano from "../../Instruments/Piano/Piano";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    playMain: {
      minHeight: "calc(100vh - 64px)",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 56px)",
      },
    },
    content: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
    },
    doubleGrid: {
      flexWrap: "nowrap",
      width: "100%",
      maxWidth: "100%",
      "& > div": {
        margin: "auto",
        padding: 0,
        height: "100%",
      },
    },
    centerText: {
      textAlign: "center",
    },
    sideStyle: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
    },
  })
);

interface RightSideProps {
  playModeChanger: () => void;
}

export const PlayerRightSide: React.FC<RightSideProps> = ({
  playModeChanger,
}) => {
  const classes = useStyles();
  return (
    <Grid
      container
      direction="column"
      justify="center"
      alignItems="center"
      className={classes.sideStyle}
    >
      <IconButton
        color="secondary"
        aria-label="play mode"
        onClick={playModeChanger}
      >
        <ArrowBackIos />
      </IconButton>
    </Grid>
  );
};
interface MiddleSideProps {
  data: ILesson;
  theme: "light" | "dark";
  playHandler: () => void;
}

export const PlayerMiddleSide: React.FC<MiddleSideProps> = (props) => {
  const { data, theme, playHandler } = props;

  const classes = useStyles();

  return (
    <Grid
      container
      direction="column"
      justify="center"
      alignItems="center"
      className={classes.content}
    >
      <Grid item xs={3} className={classes.doubleGrid}>
        <Grid container justify="center" alignItems="center">
          <Grid item xs={9}>
            <Typography variant="h4">{data.title}</Typography>
          </Grid>
          <Grid item xs={3} className={classes.centerText}>
            <IconButton
              color="secondary"
              aria-label="play song"
              onClick={() => playHandler()}
            >
              <PlayCircleOutline style={{ fontSize: "1.8em" }} />
            </IconButton>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={6} className={classes.doubleGrid}>
        <Grid container justify="center" alignItems="center">
          <Grid item xs={12}>
            <Piano theme={theme} octave={5} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={3} className={classes.doubleGrid}>
        <Grid container justify="center" alignItems="center">
          <Grid item xs={6}>
            <Typography>{data.excerpt}</Typography>
          </Grid>
          <Grid item xs={6} className={classes.centerText}>
            <Fingers theme={theme} />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};

interface LeftSideProps {
  data: ILesson;
  course?: ICourse;
  pathname: string;
  requestLesson: (path: PathFinder) => void;
}

export const PlayerLeftSide: React.FC<LeftSideProps> = (props) => {
  const [drawer, setDrawer] = useState(false);

  const toggleDrawer = (
    event: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    if (event.type === "keydown") {
      return;
    }
    if (drawer) {
      setDrawer(false);
    } else {
      setDrawer(true);
    }
  };

  const { data, ...rest } = props;

  const disableList = props.course ? false : true;
  const classes = useStyles();

  return (
    <Grid
      container
      direction="column"
      justify="space-around"
      alignItems="center"
      className={classes.sideStyle}
    >
      <Slider
        color="secondary"
        orientation="vertical"
        aria-label="music seeker"
        defaultValue={20}
        style={{ height: "40%" }}
      />
      <>
        <IconButton
          color="secondary"
          aria-label="play mode"
          onClick={(e) => toggleDrawer(e)}
          disabled={disableList}
        >
          <FormatListNumberedRtl />
        </IconButton>
        <Drawer anchor="left" open={drawer} onClose={() => setDrawer(false)}>
          <SongListSelector {...rest} lessonId={data?.id} />
        </Drawer>
      </>
    </Grid>
  );
};
