import React from "react";
import { connect } from "react-redux";
import { ThunkDispatch } from "redux-thunk";
import { bindActionCreators } from "redux";
import { ConnectedRouter } from "connected-react-router";
import { SnackbarProvider } from "notistack";
import jssRTL from "jss-rtl";
import { create } from "jss";
import { history } from "./_store/store.config";
import AppRouter from "./router";
import "./App.css";
import {
  Box,
  createStyles,
  jssPreset,
  makeStyles,
  responsiveFontSizes,
  StylesProvider,
  Theme,
  ThemeProvider,
} from "@material-ui/core";
import { LightTheme, DarkTheme } from "./components/Layout/Themes";
import Header from "./components/Layout/Header/Header";
import Notifier from "./components/Layout/Notifier";
import Breadcrumb from "./components/Layout/Breadcrumbs";
import LessonPlayerMode from "./components/PlayMode/LessonPlayMode";
import { AppActions, AppState } from "./_types";
import { changeMode } from "./_actions/player.actions";

const jss = create({ plugins: [...jssPreset().plugins, jssRTL()] });

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    app: {
      width: "100vw",
      height: "100vh",
      overflowX: "hidden",
      display: "flex",
      flexDirection: "column",
      overflow: "hidden",
    },
    mainBody: {
      height: "inherit",
      overflowX: "hidden",
      overflowY: "auto",
    },
  })
);

interface AppTheme {
  playMode: boolean;
  theme: "light" | "dark";
}
type Props = AppTheme & LinkDispatchProps;

const App = ({ theme, playMode, changeMode }: Props) => {
  const classes = useStyles();
  const currentTheme = theme === "light" ? LightTheme : DarkTheme;
  const responsTheme = responsiveFontSizes(currentTheme);
  let currentLocation = "/";
  history.listen((location) => {
    if (playMode) {
      changeMode(false);
    }
  });
  return (
    <ConnectedRouter history={history}>
      <StylesProvider jss={jss}>
        <ThemeProvider theme={responsTheme}>
          <SnackbarProvider>
            {playMode ? (
              <LessonPlayerMode />
            ) : (
              <Box className={classes.app}>
                <Header />
                <Breadcrumb />
                <Notifier />
                <Box id="main-body" className={classes.mainBody}>
                  <AppRouter />
                </Box>
              </Box>
            )}
          </SnackbarProvider>
        </ThemeProvider>
      </StylesProvider>
    </ConnectedRouter>
  );
};

interface LinkDispatchProps {
  changeMode: (mode: boolean) => void;
}

const mapStateToProps = (state: AppState) => {
  const { theme } = state.auth;
  const { playMode } = state.melodition;
  return { theme, playMode };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  changeMode: bindActionCreators(changeMode, dispatch),
});

const conectedApp = connect(mapStateToProps, mapDispatchToProps)(App);

export default conectedApp;
