export default class Song {
  audioContext: AudioContext;
  tracks: any[];
  volume: number;
  elapsedTimeSinceStart: any;
  paused: boolean;
  muted: boolean;
  loopMode: boolean
  loopStartTime: any;
  loopEndTime: any;
  decodedAudioBuffers: AudioBuffer[] | [];
  masterVolumeNode: any;
  trackVolumeNodes: any[];
  analyserNode: any;
  sampleNodes: any[];

  constructor(audioContext: AudioContext) {
    this.audioContext = audioContext;
    this.tracks = [];
    this.volume = 1;
    this.elapsedTimeSinceStart = null;
    this.paused = true;
    this.muted = false;
    this.loopMode = false;
    this.loopStartTime = null;
    this.loopEndTime = null;
    this.decodedAudioBuffers = [];
    this.masterVolumeNode = this.audioContext.createGain();
    this.trackVolumeNodes = [];
    this.analyserNode = this.audioContext.createAnalyser();
    this.sampleNodes = [];
  }
  addTrack = (data: any) => {
    this.tracks.push({
      decodedBuffer: data.buffer,
      peaks: null,
      volume: data.vol,
      panning: 0,
      muted: false,
      solo: false,
      sampleNode: null,
      volumeNode: null,
    });
  };
  loadBuffer = async (data: any) => {
    const song = this;
    await this.audioContext.decodeAudioData(
      data.buffer,
      (buffer) => {
        if (!buffer) {
          Promise.reject("error decoding file data: ");
        }
        this.decodedAudioBuffers = [...this.decodedAudioBuffers, buffer];
        this.addTrack({ buffer, vol: data.vol })
        Promise.resolve(song);
      },
      (error) => Promise.reject(error)
    );
  };
  buildGraph = () => {
    var sources = [];
    for (var i = 0; i < this.decodedAudioBuffers.length; i++) {
      var sample = this.decodedAudioBuffers[i];
      sources[i] = this.audioContext.createBufferSource();
      sources[i].buffer = sample;
      this.trackVolumeNodes[i] = this.audioContext.createGain();
      if (this.trackVolumeNodes[i]) {
        this.trackVolumeNodes[i].gain.value = this.tracks[i].volume;
      }
      sources[i].connect(this.trackVolumeNodes[i]);
      this.trackVolumeNodes[i].connect(this.masterVolumeNode);
      this.masterVolumeNode.connect(this.analyserNode);
      this.analyserNode.connect(this.audioContext.destination);
    }
    this.sampleNodes = sources;
  };
  play = (startTime: number) => {
    this.buildGraph();
    this.setTrackVolumesDependingOnMuteSoloStatus();
    this.elapsedTimeSinceStart = startTime;
    this.sampleNodes.forEach((s) => {
      s.start(0, startTime);
    });
    this.paused = false;
  };
  stop = () => {
    if (this.paused === true) return;
    this.sampleNodes.forEach((s) => {
      s.stop(0);
    });
    this.paused = true;
  };
  pause = () => {
    if (!this.paused) {
      this.stop();
    } else {
      this.play(this.elapsedTimeSinceStart);
    }
  };
  setVolume = (value: number) => {
    this.volume = value;
    this.masterVolumeNode.gain.value = value;
  };
  setMasterVolume = (val: number) => {
    var fraction;
    if (val === undefined) {
      fraction = 80 / 100;
    } else {
      fraction = val / 100;
    }
    this.setVolume(fraction * fraction);
  };
  setVolumeOfTrack = (value: number, trackNumber: number) => {
    if (this.trackVolumeNodes[trackNumber] !== undefined) {
      this.trackVolumeNodes[trackNumber].gain.value = value;
      this.tracks[trackNumber].volume = value;
    }
  };
  getUrlsOfTracks = () => {
    var urls: string[] = [];
    this.tracks.forEach((track) => {
      urls.push(track.url);
    });
    return urls;
  };
  getDuration = () => {
    if (this.decodedAudioBuffers !== undefined) {
      let dur = 0;
      for (let i = 0; i < this.decodedAudioBuffers.length; i++) {
        if (dur < this.decodedAudioBuffers[i].duration)
          dur = this.decodedAudioBuffers[i].duration;
      }
      return dur;
    }
    return undefined;
  };
  getNbTracks = () => {
    return this.tracks.length;
  };
  toggleLoopMode = () => {
    this.loopMode = !this.loopMode;
  };
  toggleMute = () => {
    this.muted = !this.muted;
    if (this.muted) {
      this.masterVolumeNode.gain.value = 0;
    } else {
      this.masterVolumeNode.gain.value = this.volume;
    }
  };
  togglePause = () => {
    this.paused = !this.paused;
  };
  setTrackVolumesDependingOnMuteSoloStatus = () => {
    var thereIsSolo = false;
    var nbTracks = this.getNbTracks();
    for (var i = 0; i < nbTracks; i++) {
      if (this.tracks[i].solo) {
        thereIsSolo = true;
        break;
      }
    }
    if (thereIsSolo) {
      for (let i = 0; i < nbTracks; i++) {
        if (this.tracks[i].solo) {
          this.trackVolumeNodes[i].gain.value = this.tracks[i].volume;
        } else {
          this.trackVolumeNodes[i].gain.value = 0;
        }
      }
      return;
    }
    for (let i = 0; i < nbTracks; i++) {
      if (this.trackVolumeNodes[i] !== undefined) {
        if (this.tracks[i].muted) {
          this.trackVolumeNodes[i].gain.value = 0;
        } else {
          this.trackVolumeNodes[i].gain.value = this.tracks[i].volume;
        }
      }
    }
  };
}