import { ResetPlayer } from "../../../_helpers/playerApi";
import { FingersClass } from "../Game/Canvas";
import Game from "../Game/Game";

type Partial<T> = {
    [P in keyof T]: T[P];
};

export class Melodition extends ResetPlayer {
    public game?: Game;
    public loaded: boolean = false;
    public onAir: boolean = false;
    public score: any[] = [];
    public handRefs: any = [];
    public seekBar?: any;
    public forwardBtn?: any;
    public keyRefs: SVGGElement[] = [];
    public keyboardDiv?: HTMLDivElement;
    public seekValue: number = 0;
    public touchedKeys: any[] = [];
    private firstDelay!: NodeJS.Timeout;
    private tickID!: NodeJS.Timeout;
    private currentBeat: number = 0;
    private tickerDelay: number = 1;
    private tickerStep: number = 0;
    private animeFrame: number = 0;
    private nextWhen: number = 0;
    private nextBeat: number = 0;
    private anime: number[] = [];
    private yPos: number = 0;
    private sentWhen: number = 0;
    private measures: number = 16;
    private finger: FingersClass[] = [];
    private midiKeys: any[] = [];
    assigner = (melo: Partial<any>) => {
        Object.assign(this, melo);
    }
    startPlay = () => {
        if (this.onAir || !this.loaded) {
            console.log("on air already");
            return;
        }
        this.score = [];
        this.resetNodeValues();
        this.nextBeat = 0;
        this.nextWhen = 0;
        this.game?.assigner({ counter: 0 })
        if (this.audioContext.state === "suspended") {
            console.log("audioContext.resume");
            this.audioContext.resume();
        }
        this.onAir = true;
        const beatDur = ((4 * 60) / (this.tempo * this.zaribTempo)) * 1000;
        this.firstDelay = setTimeout(() => {
            this.playAllTracks(0.03);
        }, beatDur);
        this.queueNextBeats();
        if (this.playMode) {
            this.game?.drawBack();
            this.game?.pushNote();
            this.animeFrame = requestAnimationFrame(this.moveSVG);
        } else {
            this.animeFrame = requestAnimationFrame(this.moveFingers);
        }
        this.onAir = true;
    }
    stopPlay = () => {
        if (!this.onAir || !this.loaded) {
            console.log("on air already");
            return;
        }
        this.finger.forEach((fin, i) => {
            fin.hide()
        });
        this.seekValue = this.currentBeat - 1;
        this.onAir = false
        this.anime = [];
        this.finger = [];
        clearTimeout(this.firstDelay);
        this.stopAllTracks();
        cancelAnimationFrame(this.animeFrame);
        clearTimeout(this.tickID);
        this.player.cancelQueue(this.audioContext);
        this.onAir = false;
    }
    stopAllTracks = () => {
        if (this.currentSong === null) return;
        this.currentSong.stop();
        this.currentSong.elapsedTimeSinceStart = 0;
    }
    playAllTracks = (startTime: number) => {
        if (this.currentSong === null) return;
        this.currentSong.setMasterVolume(100);
        this.currentSong.play(startTime);
        for (let i = 0; i < this.currentSong.getNbTracks(); i++) { }
    }
    rakeMouseWheel = (e: React.WheelEvent) => {
        const sH = this.sL * 16;
        this.seekValue = this.seekValue + (e.deltaY > 0 ? -1 : 1)
        this.seekValue = this.seekValue < 0 ? 0 :
            this.seekValue > sH ? sH : this.seekValue
        const seekY = this.seekValue * this.tapSize;
        this.seekMove(this.seekValue);
        this.seekSVG(seekY);
    }
    seekSVG = (y: number) => {
        if (!this.onAir) {
            this.game?.updateFullSong(y)
        }
    }
    resetNodeValues = () => {
        for (let i = 0; i < this.trackInfo.length; i++) {
            this.trackInfo[i].audioNode.gain.setValueAtTime(
                this.trackInfo[i].volume / 100,
                0
            );
        }
        for (let i = 0; i < this.drumInfo.length; i++) {
            this.drumInfo[i].audioNode.gain.setValueAtTime(
                this.drumVolumes[i] / 100,
                0
            );
        }
        this.master.band32.gain.setValueAtTime(this.equalizer[0], 0);
        this.master.band64.gain.setValueAtTime(this.equalizer[1], 0);
        this.master.band128.gain.setValueAtTime(this.equalizer[2], 0);
        this.master.band256.gain.setValueAtTime(this.equalizer[3], 0);
        this.master.band512.gain.setValueAtTime(this.equalizer[4], 0);
        this.master.band1k.gain.setValueAtTime(this.equalizer[5], 0);
        this.master.band2k.gain.setValueAtTime(this.equalizer[6], 0);
        this.master.band4k.gain.setValueAtTime(this.equalizer[7], 0);
        this.master.band8k.gain.setValueAtTime(this.equalizer[8], 0);
        this.master.band16k.gain.setValueAtTime(this.equalizer[9], 0);
    }
    queueNextBeats = () => {
        if (this.onAir) {
            let beat16duration = (4 * 60) / (this.tempo * this.zaribTempo) / 16;
            let pieceLen16 = this.songLength + 1;
            let t = this.audioContext.currentTime;
            if (this.nextWhen < t) {
                this.nextWhen = t;
            }
            while (this.sentWhen < t + beat16duration) {
                this.sendNextBeats(this.nextWhen, this.nextBeat, this.nextBeat);
                this.nextWhen = this.sentWhen + beat16duration;
                this.nextBeat = this.nextBeat + 1;
                if (this.playMode && (this.nextBeat / 16) % 1 === 0) {
                    this.game?.pushNote();
                }
                if (this.nextBeat > pieceLen16) {
                    this.stopPlay();
                }
            }
            let wait = 0.5 * 1000 * (this.nextWhen - t);
            this.tickerStep++;
            if (this.tickerStep >= this.tickerDelay) {
                this.tickerStep = 0;
            }
            this.tickID = setTimeout(() => {
                this.seekMove(this.nextBeat);
                this.queueNextBeats();
                if (!this.playMode) this.showFingers();
            }, wait);
        }
    }
    sendNextBeats = (when: number, startBeat: number, endBeat: number) => {
        this.sentWhen = when;
        let N = (4 * 60) / (this.tempo * this.zaribTempo);
        let beatLen = (1 / 16) * N;
        this.currentBeat = startBeat;
        this.storeDrums.forEach((hit) => {
            if (hit.beat >= startBeat && hit.beat <= endBeat) {
                let channel = this.findDrumInfoHand(hit.drum);
                let zones = channel.sound;
                if (channel.info && window[channel.info.variable]) {
                    zones = window[channel.info.variable];
                }
                let r = 1.0 - Math.random() * 0.2;
                this.player.queueWaveTable({
                    audioContext: this.audioContext,
                    target: channel.audioNode,
                    preset: zones,
                    when: when + beatLen * (hit.beat - startBeat),
                    pitches: channel.pitch,
                    duration: channel.length,
                    volume: r * channel.volumeRatio,
                });
            }
        })
        let notes = [];
        for (let i = 0; i < this.storeTracks.length; i++) {
            let note = this.storeTracks[i];
            if (note.beat >= startBeat && note.beat <= endBeat) {
                notes.push(note);
            }
        }
        notes.sort(function (n1, n2) {
            let r = 1000 * (n1.beat - n2.beat) + 100000 * (n1.track - n2.track);
            if (n1.beat === n2.beat) {
                r = r + (n1.pitch - n2.pitch);
            }
            return r;
        });
        let currentBeat = -1;
        let currentTrack = -1;
        let inChordCount = 0;
        notes.forEach(note => {
            let channel = this.trackInfo.find(trc => trc.nn === note.track);
            if (note.beat !== currentBeat || note.track !== currentTrack) {
                currentBeat = note.beat;
                currentTrack = note.track;
                inChordCount = 0;
                if (note.track === this.findTrackInfoHand().nn) {
                    this.anime.push(note.beat);
                }
            }
            let zones = channel.sound;
            if (channel.info && window[channel.info.variable]) {
                zones = window[channel.info.variable];
            }
            let shift = [
                {
                    when: note.length * beatLen,
                    pitch: note.shift + channel.octave * 12 + note.pitch,
                },
            ];
            let r = 0.6 - Math.random() * 0.2;
            this.player.queueWaveTable({
                audioContext: this.audioContext,
                target: channel.audioNode,
                preset: zones,
                when:
                    when +
                    beatLen * (note.beat - startBeat) +
                    inChordCount * channel.inChordDelay,
                pitches: channel.octave * 12 + note.pitch,
                duration: 0.075 + note.length * beatLen,
                volume: r * channel.volumeRatio,
                slides: shift,
            });
            inChordCount++;
        })
    }
    showFingers = () => {
        const fingers = this.storeFingers[this.TrackID];
        if (this.anime.length !== 0) {
            this.anime.forEach((nBeat) => {
                const beat = this.fingerNotes[nBeat];
                if (beat) {
                    let handArr: any = {};
                    for (let fin in fingers) {
                        if (fingers[fin][nBeat]) {
                            const hand = this.handRefs[fin];
                            const key = fingers[fin][nBeat];
                            handArr[key] = { finger: hand, hand: hand.getAttribute("hand") };
                        }
                    }
                    beat.forEach((b: any) => {
                        const len = b.length;
                        const pitch = b.pitch + 36;
                        const note = this.keyRefs[pitch];
                        if (handArr[pitch]) {
                            this.finger.push(new FingersClass({
                                note: note,
                                len: len,
                                hand: handArr[pitch],
                                beat: this.currentBeat
                            }));
                        }
                    });
                }
                this.anime = this.anime.filter((item) => item !== nBeat);
            });
        }
    }
    moveFingers = () => {
        if (this.onAir) {
            this.finger.forEach((fin, i) => {
                if (Melo.currentBeat >= fin.end || !Melo.onAir) {
                    fin.hide()
                    this.finger.splice(i, 1)
                } else {
                    fin.show()
                }
            });
            requestAnimationFrame(this.moveFingers);
        }
    }
    findTrackInfoHand = (): any => {
        let track = null
        this.trackInfo.forEach(trc => {
            if (trc.nn === this.TrackID) {
                track = trc;
            }
        });
        return track;
    }
    findDrumInfoHand = (order: number): any => {
        let drum = null
        this.drumInfo.forEach(drm => {
            if (drm.id === order) {
                drum = drm;
            }
        });
        return drum;
    }

    seekMove = (value: number) => {
        if (value < this.songLength)
            if (this.seekBar) this.seekBar.changeSeeker(null, value);
    }
    moveSVG = () => {
        if (this.game && this.onAir) {
            const N = (4 * 60) / (this.tempo * this.zaribTempo);
            const beatLen = (1 / 16) * N;
            const c16 = 16 * this.measures;
            let diff =
                this.nextBeat +
                (this.audioContext.currentTime - this.nextWhen) / beatLen;
            while (diff < 0) {
                diff = diff + c16;
            }
            if (diff > this.songLength - 1) {
                diff = this.songLength - 1;
            }
            if (this.game.counter < 4 && diff > 100) diff = 0;
            let y = Math.floor(diff * this.tapSize);
            this.yPos = y;
            this.game.playGame(y);
            requestAnimationFrame(this.moveSVG);
        }
    }
    midiNoteOn = (pitch: any, velocity = 0.7) => {
        if (velocity === 0) {
            this.midiNoteOff(pitch);
        } else {
            this.midiNoteOff(pitch);
            if (this.onAir) this.touchedKeys.push({ note: pitch, y: this.yPos });
            let channel = this.findTrackInfoHand();
            if (channel) {
                let zones = channel.sound;
                if (channel.info && window[channel.info.variable]) {
                    zones = window[channel.info.variable];
                }
                this.midiKeys[pitch] = this.player.queueWaveTable({
                    audioContext: this.audioContext,
                    target: channel.audioNode,
                    preset: zones,
                    when: 0,
                    pitches: pitch,
                    duration: 10,
                    volume: velocity,
                });
                if (this.keyRefs[pitch]) {
                    const elem: any = this.keyRefs[pitch];
                    const cColor = elem.children.back.getAttribute("fill");
                    let bColor = "red";
                    if (cColor !== "white" && cColor !== "black") bColor = "green";
                    elem.children.back.style.fill = bColor;
                }
            }
        }
    }
    midiNoteOff = (pitch: any) => {
        if (this.midiKeys[pitch]) {
            this.midiKeys[pitch].cancel();
            this.touchedKeys.forEach((x, i) => {
                const notePitch = this.touchedKeys[i].note;
                if (pitch === notePitch) this.touchedKeys.splice(i, 1);
            });
        }
        if (this.keyRefs[pitch]) {
            const elem: any = this.keyRefs[pitch];
            if (elem) {
                elem.children.back.style.fill = "";
            }
        }
    }
}
const Melo = new Melodition()
export default Melo;