import React, { Component, ComponentType } from "react";
import { ThunkDispatch } from "redux-thunk";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {
  ArrowBackIos,
  PlayCircleOutline,
  PauseCircleOutline,
} from "@material-ui/icons";
import {
  createStyles,
  Grid,
  Theme,
  Slider,
  IconButton,
  WithStyles,
  withStyles,
} from "@material-ui/core";
import PianoLines from "../Instruments/Piano/PianoLines";
import {
  changeMode,
  startPlayer,
  stopPlayer,
} from "../../_actions/player.actions";
import { AppActions, AppState } from "../../_types";
import { ILesson } from "../../_types/lesson.types";
import { ICourse } from "../../_types/course.types";
import Piano from "../Instruments/Piano/Piano";
import PlayerHeader from "./PlayerHeader";
import { IMelodition } from "../../_types/player.types";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      width: "100vw",
      height: "100vh",
      overflow: "hidden",
      backgroundColor: theme.palette.background.paper,
    },
    main: {
      width: "100vw",
      overflow: "hidden",
      minHeight: "calc(100vh - 64px)",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 56px)",
      },
      display: "flex",
    },
    content: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
    },
    doubleGrid: {
      flexWrap: "nowrap",
      width: "100%",
      maxWidth: "100%",
      "& > div": {
        margin: "auto",
        padding: 0,
        height: "100%",
      },
    },
    centerText: {
      textAlign: "center",
    },
  });

interface LessonPlayerModeProps extends WithStyles<typeof useStyles> {
  history: string;
  lesson?: ILesson;
  course?: ICourse;
  theme: "light" | "dark";
  melodition: IMelodition;
}

interface States {
  list: boolean;
}

type Props = LessonPlayerModeProps & LinkDispatchProps;

class LessonPlayerMode extends Component<Props, States> {
  constructor(props: Props) {
    super(props);
    this.state = {
      list: false,
    };
  }
  svgWidth = (i: number): number => {
    const num = i === 1 ? 668 : 584;
    return num;
  };

  toggleDrawer = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    if (event.type === "keydown") {
      return;
    }
    if (this.state.list) {
      this.setState({ list: false });
    } else {
      this.setState({ list: true });
    }
  };
  playmodeChanger = () => {
    this.props.changeMode(false);
  };

  playHandler = () => {
    if (this.props.melodition.onAir) {
      this.props.stopPlay();
    } else {
      this.props.startPlay();
    }
  };

  render() {
    const { theme, classes, melodition } = this.props;
    return (
      <div className={classes.root}>
        <PlayerHeader modeChanger={this.playmodeChanger} />
        <Grid container className={classes.main}>
          <Grid item xs={1}>
            <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
              className={classes.content}
            >
              <IconButton
                color="secondary"
                aria-label="play song"
                onClick={() => this.playHandler()}
              >
                {melodition.onAir ? (
                  <PauseCircleOutline style={{ fontSize: "1.8em" }} />
                ) : (
                  <PlayCircleOutline style={{ fontSize: "1.8em" }} />
                )}
              </IconButton>
            </Grid>
          </Grid>
          <Grid item xs={10}>
            <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
              className={classes.content}
            >
              <Grid item xs={12}>
                <PianoLines />
                <Piano theme={theme} octave={5} />
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={1}>
            <Grid
              container
              direction="column"
              justify="space-around"
              alignItems="center"
              className={classes.content}
            >
              <Slider
                color="secondary"
                orientation="vertical"
                aria-label="music seeker"
                defaultValue={20}
                style={{
                  height: "40%",
                }}
              />
              <IconButton color="secondary" aria-label="play mode">
                <ArrowBackIos />
              </IconButton>
            </Grid>
          </Grid>
        </Grid>
      </div>
    );
  }
}

interface LinkDispatchProps {
  changeMode: (mode: boolean) => void;
  startPlay: () => void;
  stopPlay: () => void;
}

const mapStateToProps = (
  state: AppState,
  ownProps: Props
): LessonPlayerModeProps => ({
  history: state.router.location.pathname,
  classes: ownProps.classes,
  melodition: state.melodition,
  theme: state.auth.theme,
  lesson: state.single.lesson.data,
  course: state.single.course.data,
});

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  changeMode: bindActionCreators(changeMode, dispatch),
  startPlay: bindActionCreators(startPlayer, dispatch),
  stopPlay: bindActionCreators(stopPlayer, dispatch),
});

export default (connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(LessonPlayerMode)) as unknown) as ComponentType<
  unknown
>;
