import { ResetPlayer } from "../_helpers/playerApi";

export const REQUEST_PLAYER = "REQUEST_PLAYER";
export const RESET_PLAYER = "RESET_PLAYER";
export const START_PLAYER = "START_PLAYER";
export const STOP_PLAYER = "STOP_PLAYER";
export const REQUEST_PLAYMODE = "REQUEST_PLAYMODE";

export interface IMelodition {
  playMode: false,
  loading: true,
  octave: number,
  onAir: boolean
}

export interface PlayerDataInfo {
  fingers: string[];
  song: {
    content: string;
    edited: string;
    id: number;
    name: string;
  }
  track: {
    id: number;
    name: string;
  }
}
export interface IQueue {
  audioContext: AudioContext,
  target: any,
  preset: any,
  when: any,
  pitches: any,
  duration: any,
  volume: any,
  slides?: any
}
export interface IEnvelope {
  audioContext: AudioContext,
  envelope: any,
  zone: any,
  volume: any,
  startWhen: any,
  waveDuration: any,
  duration: any
}
export interface ISetup {
  audioContext: AudioContext,
  target: any,
  when: any,
  duration: any
}

export interface StartPlayers {
  type: typeof START_PLAYER;
}
export interface StopPlayers {
  type: typeof STOP_PLAYER;
}

export interface ResetPlayers {
  type: typeof RESET_PLAYER;
}
export interface RequestPlayers {
  type: typeof REQUEST_PLAYER;
  payload: ResetPlayer;
}
export interface PlayModeAction {
  type: typeof REQUEST_PLAYMODE;
  mode: boolean;
}

export type PlayerActionTypes =
  | RequestPlayers
  | StartPlayers
  | StopPlayers
  | ResetPlayers
  | PlayModeAction;
